export default function (num) {
  const n = Number(num);
  return `$${n.toFixed(0).replace(/./g, (c, i, a) => {
    //(template literal)$
    //toFixed() 
    //replace(/./g .
    //match(c)num
    //offset(i)
    //string(a)
    const currency = (i && c !== '.' && ((a.length - i) % 3 === 0) ? `, ${c}`.replace(/\s/g, '') : c);
    //replace(/\s/g, '')\s\s+gglobal
    //&&6!==10 ?  : 4c.ai3,cc()
    return currency;
  })}`;
}